/*colors*/
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2"), url("../fonts/oswaldregular.woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2"), url("../fonts/oswaldmedium.woff");
}
body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  color: #111111;
}
img {
  max-width: 100%;
  height: auto;
}
.container {
  width: 280px;
  margin-top: 0;
  margin-right: auto;
  margin-bottom: 0;
  margin-left: auto;
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 768px) {
  .container {
    width: 708px;
  }
}
@media (min-width: 1300px) {
  .container {
    width: 1220px;
  }
}
.main-nav {
  position: relative;
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.main-nav__item {
  border: 1px solid #e6e6e6;
  border-bottom: none;
}
.main-nav__item a {
  display: block;
  text-decoration: none;
  color: #222222;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
  text-align: center;
  padding-top: 22px;
  padding-bottom: 22px;
}
.main-nav__item a:hover {
  opacity: 0.6;
}
.main-nav__item a:active {
  opacity: 0.3;
}
.main-nav__toggle {
  position: absolute;
  width: 24px;
  height: 18px;
  border: none;
  right: 20px;
  top: -45px;
  padding: 0;
  z-index: 2;
  background-color: transparent;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  width: 24px;
  height: 2px;
  background-color: #222222;
  box-shadow: 0 8px 0 0 #222222, 0 16px 0 0 #222222;
}
.main-nav--opened .main-nav__toggle {
  right: 22px;
  top: -44px;
  height: 18px;
  width: 18px;
}
.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 9px;
  left: -3px;
  width: 24px;
  height: 2px;
  transform: rotate(45deg);
  background-color: #222222;
}
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 9px;
  right: -3px;
  width: 24px;
  height: 2px;
  transform: rotate(-45deg);
  background-color: #222222;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__list {
  display: flex;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav {
    width: 516px;
  }
  .main-nav__list {
    display: flex;
    justify-content: flex-end;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__item {
    border: none;
    padding-left: 35px;
  }
  .main-nav__item a {
    font-size: 18px;
    font-weight: 400;
    line-height: 24.86px;
    padding-bottom: 7px;
    padding-top: 7px;
  }
  .main-nav__item--active a {
