  border-right: none;
  background-color: #ffffff;
  right: -60px;
  top: 3px;
}
.slider__toggle-right {
  position: relative;
}
.slider__toggle-right::before {
  position: absolute;
  content: "";
  width: 42px;
  height: 14px;
  box-sizing: border-box;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  border: 1px solid #cdcdcd;
  border-left: none;
  background-color: #ffffff;
  left: -60px;
  top: 3px;
}
.slider__toggle-right::after {
  position: absolute;
  display: none;
  content: "";
  width: 35px;
  height: 6px;
  border-radius: 3px;
  background-color: #68b738;
  top: 7px;
  left: -57px;
}
