  position: absolute;
  display: none;
  content: "";
  width: 35px;
  height: 6px;
  border-radius: 3px;
  background-color: #68b738;
  top: 7px;
  right: -58px;
}
.slider__before:checked ~ .slider__toggles .slider__toggle-left::after {
  display: block;
}
.slider__after:checked ~ .slider__toggles .slider__toggle-right::after {
  display: block;
}
.slider__before:checked ~ .slider__slide .slider__image-after {
  display: none;
}
.slider__before:checked ~ .slider__slide .slider__image-before {
  display: block;
}
.slider__after:checked ~ .slider__slide .slider__image-before {
  display: none;
}
