.fieldset {
  border: none;
  border-top: 3px solid #68b738;
  margin-left: 19px;
  padding-left: 0;
  padding-right: 20px;
  padding-top: 30px;
  margin-right: 0;
}
.fieldset--name {
  border: none;
  padding-top: 0;
  margin-bottom: 15px;
}
.fieldset--goal {
  border: 1px solid #cdcdcd;
  margin-left: 0;
  margin-bottom: 10px;
  margin-right: 0;
  padding-left: 19px;
  padding-top: 25px;
  padding-bottom: 27px;
}
.fieldset--contacts {
  margin-bottom: 20px;
}
.fieldset--checkbox {
  padding-top: 34px;
  margin-bottom: 30px;
}
.fieldset__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.fieldset__legend {
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  color: #222222;
  padding-right: 10px;
  text-align: left;
}
.fieldset__legend--contacts {
  width: 200px;
  box-sizing: border-box;
  padding-top: 18px;
  margin-bottom: 4px;
}
.fieldset__legend--checkbox {
  padding-bottom: 5px;
  padding-right: 24px;
}
.fieldset__label {
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  color: #111111;
  text-transform: uppercase;
}
.fieldset__item {
  display: flex;
  align-items: baseline;
  margin-bottom: 14px;
}
.fieldset__item:last-child {
  margin-bottom: 0;
}
.fieldset__item--radio,
.fieldset__item--checkbox {
  margin-bottom: 25px;
}
.fieldset__item input[placeholder] {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  color: #444444;
  text-transform: uppercase;
}
.fieldset__item input[type="text"],
.fieldset__item input[type="number"],
.fieldset__item input[type="tel"],
.fieldset__item input[type="email"],
.fieldset__item input[required] {
  border: 1px solid rgba(205, 205, 205, 0.5);
  width: 100%;
  box-sizing: border-box;
  padding: 12px;
  padding-bottom: 13px;
  margin-left: 13px;
  outline: none;
}
.fieldset__item input[type="text"]:hover,
.fieldset__item input[type="number"]:hover,
.fieldset__item input[type="tel"]:hover,
.fieldset__item input[type="email"]:hover,
.fieldset__item input[required]:hover {
  border: 1px solid #cdcdcd;
}
.fieldset__item input[type="text"]:focus,
.fieldset__item input[type="number"]:focus,
.fieldset__item input[type="tel"]:focus,
.fieldset__item input[type="email"]:focus,
.fieldset__item input[required]:focus {
  border: 2px solid #444444;
  opacity: 1;
}
.fieldset__item input[type="text"]:focus ~ .fieldset__icon,
.fieldset__item input[type="number"]:focus ~ .fieldset__icon,
.fieldset__item input[type="tel"]:focus ~ .fieldset__icon,
.fieldset__item input[type="email"]:focus ~ .fieldset__icon,
.fieldset__item input[required]:focus ~ .fieldset__icon {
  fill: #68b738;
}
.fieldset__item input[type="text"]:invalid,
.fieldset__item input[type="number"]:invalid,
.fieldset__item input[type="tel"]:invalid,
.fieldset__item input[type="email"]:invalid,
.fieldset__item input[required]:invalid {
  border: 2px solid #ff8282;
}
.fieldset__item input[type="text"]:invalid ~ .fieldset__icon,
.fieldset__item input[type="number"]:invalid ~ .fieldset__icon,
.fieldset__item input[type="tel"]:invalid ~ .fieldset__icon,
.fieldset__item input[type="email"]:invalid ~ .fieldset__icon,
.fieldset__item input[required]:invalid ~ .fieldset__icon {
  fill: #ff8282;
}
.fieldset--name label,
.fieldset--contacts label {
  white-space: pre;
  word-break: normal;
}
.fieldset__item--checkbox input,
.fieldset__item--radio input {
  display: none;
}
.fieldset__item--checkbox input:disabled ~ .fieldset__label,
.fieldset__item--radio input:disabled ~ .fieldset__label {
  opacity: 0.3;
}
.fieldset__label--radio,
.fieldset__label--checkbox {
  position: relative;
  padding-left: 46px;
}
.fieldset__label--radio:hover::before,
.fieldset__label--checkbox:hover::before {
  border: 1px solid #bababa;
}
.fieldset__label--radio::before,
.fieldset__label--checkbox::before {
  position: absolute;
  content: "";
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  left: 0;
  top: -5px;
}
.fieldset__label--radio::before {
  border-radius: 50%;
}
.fieldset__item--radio input:checked ~ .fieldset__label--radio::after {
  position: absolute;
  content: "";
  box-sizing: border-box;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #68b738;
  left: 9px;
  top: 4px;
}
.fieldset__item--checkbox input:checked ~ .fieldset__label--checkbox::after {
  position: absolute;
  content: "";
  box-sizing: border-box;
  width: 21px;
  height: 16px;
  background-image: url("../img/icon-tick.svg");
  background-repeat: no-repeat;
  left: 5px;
  top: 3px;
}
.fieldset--comment {
  margin-bottom: 25px;
  padding-bottom: 0;
}
.fieldset__legend--comment {
  padding-bottom: 5px;
  padding-right: 22px;
}
.fieldset__textarea {
  width: 280px;
  height: 150px;
  box-sizing: border-box;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  color: #444444;
  padding-top: 12px;
  padding-left: 15px;
  border: 1px solid #cdcdcd;
}
.fieldset__item--contacts {
  position: relative;
}
.fieldset__icon {
  position: absolute;
  right: 17px;
  top: 16px;
}
.fieldset__icon--phone {
  right: 13px;
  top: 14px;
}
