body {
  margin: 0;
  padding: 0;
  min-width: 320px;

  font-family: "Oswald", "Arial", sans-serif;
  .font(14px, 400, 18px);
  color: @1;
}


img {
  max-width: 100%;
  height: auto;
}

.container {
  width: 280px;
  .center-block();
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}


@media (min-width: @tablet-width) {
  .container {
    width: 708px;
  }
}

@media (min-width: @desktop-width) {
  .container {
    width: 1220px;
  }
}
